/* "colorhunt 212" light on dark theme
 */
@import '../consts/consts--colors--colorhunt--212.css';
@import '../consts/consts--colors--modifiers.css';

@import 'theme--common.css';

[data-o-theme="dark--colorhunt212"] {

	--o⋄color⁚fg--main: var(--o⋄color⁚co212__beige);
	xx--o⋄color⁚fg--strong: var(--o⋄color⁚fg--main);

	--o⋄color⁚fg--secondary: hsla(42, 100%, 87%, 0.66);
	--o⋄color⁚fg--ancillary: hsla(42, 100%, 87%, 0.33);

	--o⋄color⁚fg--accent: var(--o⋄color⁚co212__beige);
	--o⋄color⁚fg--activity-outline: var(--o⋄color⁚co212__blue);
	--o⋄color⁚bg--main: var(--o⋄color⁚co212__brown);
	xxx--o⋄color⁚bg--main: var(--o⋄color⁚co212__dark);
	--o⋄color⁚bg--code: #75636a;

	/* "app" mode, we don't emphasize links */
	--o⋄color⁚fg--link:          var(--o⋄color⁚co212__beige);
	--o⋄color⁚fg--link--visited: var(--o⋄color⁚co212__beige);

	--o⋄color⁚fg--error: red;
	--o⋄color⁚fg--warning: var(--o⋄color⁚boz__yellow);
	--o⋄color⁚fg--info: var(--o⋄color⁚boz__blue);
	--o⋄color⁚fg--success: var(--o⋄color⁚boz__green);
}
